import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import Home from "@/views/home/index.vue";

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "/",
    redirect:"/home"
  },
  {
    path: "/home",
    name: "home",
    component: Home,
  },
  {
    path: "/player",
    name: "player",
    component: () => import("@/views/play/index.vue"),
  },
  {
    path: "*",
    redirect: "/home",
  },
];

const router = new VueRouter({
  routes,
});

export default router;
