
import Vue from "vue";

export default Vue.extend({
  name: "RecList",
  data(): SelfData {
    return {
      recList: undefined,
    };
  },
  async created() {
    const res = await this.$api.get("/personalized?limit=6");
    if (res.statusText === "OK" && res.status === 200) {
      const recList: SelfReq<SelfReqResult[]> = res.data;
      if (recList.code === 200) {
        this.recList = recList.result;
      }
    }
  },
});

export interface SelfReq<T> {
  code: number;
  result: T;
  category: number;
  hasTaste?: boolean;
}

interface SelfReqResult {
  alg: string;
  canDislike: boolean;
  copywriter: string;
  highQuality?: boolean;
  id: number;
  name: string;
  picUrl: string;
  playCount: number;
  trackCount?: number;
  trackNumberUpdateTime: number;
  type: number;
}

interface SelfData {
  recList?: SelfReqResult[];
}
