
import Vue from "vue";
export default Vue.extend({
  name: "LatestList",
  data(): any {
    return {
      latestList: [],
    };
  },
  async created() {
    this.latestList = (
      await this.$api.get("/personalized/newsong")
    ).data.result;
  },
  methods: {
    toPlayer(data: any) {
      const obj = {
        path: "/player",
        query: {
          picUrl: data.picUrl,
          name: data.name,
          id: data.id,
          nickName: data.name,
        },
      };
      this.$router.push(obj);
    },
  },
});
