
import Vue from "vue";
import _ from "lodash";

import Loading from "@/components/Loading/index.vue";
import { Toast, Search, Icon } from "vant";

Vue.use(Toast);
Vue.use(Search);
Vue.use(Icon);

export default Vue.extend({
  name: "Search",
  props: [],
  data(): any {
    return {
      searchValue: "", // 搜索框中输入的数据
      hotKeyList: [], // 热词
      isShowLoading: false, // loading是否显示
      keySonsList: [], // 根据关键字获取对应的歌曲列表
      isShowKey: true, // 是否显示热词
      historyList: [], // 搜索的历史记录
    };
  },
  components: {
    Loading,
  },
  created() {
    this.getHotKey();
  },
  methods: {
    // 获取热词
    getHotKey() {
      this.isShowLoading = true;
      this.$api
        .post("/search/hot")
        .then((res: any) => {
          if (res.data.code == 200) {
            this.hotKeyList = res.data.result.hots;
          }
          this.isShowLoading = false;
        })
        .catch((err: any) => {
          Toast("网络出小差");
          this.isShowLoading = false;
        });
    },
    subKey(val: any) {
      if (this.timer) clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        if (!val) {
          this.isShowKey = true;
          return;
        }
        this.searchValue = val;
        this.isShowKey = false;
        this.getKeyInfo();
        this.timer = null;
      }, 500);
    },
    test() {
      console.log("test...");
    },

    clearKey() {
      this.isShowKey = true;
    },
    // 当输入框失去焦点时
    saveKey() {
      if (this.searchValue && !this.historyList.includes(this.searchValue)) {
        this.historyList.push(this.searchValue);
      }
    },

    removeKey(val: any) {
      this.historyList = this.historyList.filter((item: any) => item != val);
    },

    getKeyInfo() {
      this.isShowLoading = true;
      this.$api
        .get("/cloudsearch", {
          params: {
            keywords: this.searchValue,
          },
        })
        .then((res: any) => {
          if (res.data.code == 200) {
            this.keySonsList = res.data.result.songs;
          }
          this.isShowLoading = false;
        })
        .catch((err: any) => {
          Toast("网络出小差");
          this.isShowLoading = false;
        });
    },
    toPlayer(data: any) {
      console.log("data=>", data);
      let obj = {
        path: "/player",
        query: {
          picUrl: data.al.picUrl,
          name: data.al.name,
          id: data.id,
          nickName: data.ar[0].name,
        },
      };
      this.$router.push(obj);
    },
  },
});
